@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
header {
    height: 100vh ;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ============= CTA ================= */
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}


/* ============= HeaderSocials ================= */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 1rem;
    bottom: 5rem;

}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}



/* ============= ME 修照片================= */

.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem ;
    border-radius: 17rem 17rem 0 0;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem; */

}

/* ============= Scrool down  ================= */
.scroll__down{
    position: absolute;
    right: -0.3rem;
    bottom: 8rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


 /* ============ MEDICA QUERIES (MEDIUM DEVICES ) ===============  */

 @media screen and (max-width: 1024px) {
    .header{
        height: 68vh;
    }
 }

  /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

  @media screen and (max-width: 600px) {
    .header{
        height: 100vh;
    }
    .header__socials,
    .scroll__down{
        display: none;
    }
  }
 

nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.9rem 2rem;
    z-index: 2; 
    position: fixed;
    left: 50%;
    top: 90%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 1.5rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    /* the nav icon color blue or white */
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}


/* outline, pic and words  */
.about__container{
    display:grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg, 
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    /* make the pic slightly the change degree */
    transition: var(--transition);
} 

.about__me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* based on how many project you have   repeat(2, 1fr) 2 project*/
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.9rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 1rem 0 1.6rem;
    color: var(--color-light);
}

 /* ============ MEDICA QUERIES (MEDIUM DEVICES ) ===============  */

 @media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
        /* top left & right bottom */
    }
 }

  /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

  @media screen and (max-width: 600px) {

    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
        /* top left & right  */
    }
  }


.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    /* == mean 50 50 left and right  */
    grid-gap: 10rem;
    gap: 10rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon{
    margin-top: 6px;
    color: var(--color-primary);

}

 /* ============ MEDICA QUERIES (MEDIUM DEVICES ) ===============  */

 @media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr ;
    }
    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__container{
        padding: 1rem;
    }


 }

  /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

  @media screen and (max-width: 600px) {
    .experience__container{
         grid-gap: 1rem;
         gap: 1rem;
    }
    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }

  }

.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item{
    background: var(--color-bg-variant);
    padding:1.5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* ============ MEDICA QUERIES (MEDIUM DEVICES ) ===============  */

@media screen and (max-width: 1024px) {
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }

 }

  /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

@media screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

  }


.container.testimonials__container{
    width: 50%;
    padding-bottom: 4rem;
}

.photo {
    border-radius: 1rem;
    margin: 0 auto 1rem;
}

.testimonial {
    background: var(--color-bg-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    -webkit-user-select: none;
            user-select: none;
}


 /* ============ MEDICA QUERIES (MEDIUM DEVICES ) ===============  */

 @media screen and (max-width: 1024px) {
    .container.testimonials__container{
        width: 60%;
    }
 }

  /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

  @media screen and (max-width: 600px) {
    .container.testimonials__container{
        width: var(--container-width-sm);
    }

  }

.container.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}


.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transtion);
}
.contact__option:hover{
    background: transparent;
    border-color:var(--corlor-primary-varient);
}

.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5 rem;

}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* =====================FORM ===================== */

