* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  /* name the variant */
  --color-bg: #1f1f38;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff ;
  --color-primary-variant: rgba(77, 181, 255,0.4);
  --color-white: #fff;
  --color-light:rgba(255,255,255,0.6);

  --transition: all 400ms ease;

/* screan size */
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}


html {
  scroll-behavior: smooth;

}

::-webkit-scrollbar{
  display: none;

}


body{
  /* fonts.google.com to select the size and import to first line  */
  font-family: 'Roboto', sans-serif;
  background: #1f1f38;
  background: var(--color-bg);
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
  /* background-image: url(../src/assets/bg-testure.png) if I want to add any texture  */
}


/* ================== GENRAL STYLERS============== */
.container {
  width: 75%;
  width: var(--container-width-lg);
    /* position in the middle horizontally */ 
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}


h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
}

section > h2, 
section > h5 {
  text-align: center;
  color: rgba(255,255,255,0.6);
  color: var(--color-light);
}

section > h2 {
  color: #4db5ff;
  color: var(--color-primary);
  margin-bottom: 3rem;
}


.text-light{
  color: rgba(255,255,255,0.6);
  color: var(--color-light);
}

a {
  color: #4db5ff;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);

}

a:hover{
  color: #fff;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #4db5ff;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #4db5ff;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);

}


.btn:hover{
  background: #fff;
  background: var(--color-white);
  color: #1f1f38;
  color: var(--color-bg);
  border-color: transparent;

}



.btn-primary{
  background: #4db5ff;
  background: var(--color-primary);
  color: #1f1f38;
  color: var(--color-bg);

}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}



 /* ============ MEDICA QUERIES (MEDIUM DEVICES ) ===============  */

 @media screen and (max-width: 1024px) {
  .container{
    width:86%;
    width:var(--container-width-md);
  }
  section{
    margin-top: 6rem;
  }

 }



 /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

 @media screen and (max-width: 600px) {
  .container{
    width:90%;
    width:var(--container-width-sm);
  }
  section > h2{
    margin-bottom: 2rem;
  }
  
 }
