  /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

  @media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }

    }