header {
    height: 100vh ;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ============= CTA ================= */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}


/* ============= HeaderSocials ================= */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 1rem;
    bottom: 5rem;

}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}



/* ============= ME 修照片================= */

.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem ;
    border-radius: 17rem 17rem 0 0;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem; */

}

/* ============= Scrool down  ================= */
.scroll__down{
    position: absolute;
    right: -0.3rem;
    bottom: 8rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


 /* ============ MEDICA QUERIES (MEDIUM DEVICES ) ===============  */

 @media screen and (max-width: 1024px) {
    .header{
        height: 68vh;
    }
 }

  /* ============ MEDICA QUERIES (SMALL DEVICES ) ===============  */

  @media screen and (max-width: 600px) {
    .header{
        height: 100vh;
    }
    .header__socials,
    .scroll__down{
        display: none;
    }
  }
 
